@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
.model__grid {
  display: grid;
  width: 100%;
  grid-gap: 10px;
  padding: 0.5rem;
  font-size: 1.3rem;
  color: #000;
  font-weight: 400;
  background: #3399ff;
  border: 4px solid aliceblue;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, 1fr);
  margin-top: 5rem;
}
.row ul {
  padding: 1rem;
  list-style: none;
}
.row-1,
.column-1 {
  grid-row: 1/2;
}
.row-2,
.column-2 {
  grid-row: 2/3;
}
.row-full,
.column-full {
  grid-row: 1 / span 2;
}
.title {
  text-align: center;
}
.row {
  border-radius: 0.5rem;
  border: 0.4rem solid #fff;
  background-color: #cce5ff;
}

#heading {
  margin-top: 6rem;
  padding: 1rem 6rem 2rem 6rem;
  background-color: #3effdaba;
  border-top: solid white 2px;
  border-bottom: solid white 2px;
  box-shadow: 5px 10px 5px rgb(0 0 0 / 50%);
}
#pageTitle {
  color: white;
  font-size: 8rem;
  font-family: "Bebas Neue", cursive;
}

